@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{display:flex;border-bottom:2px solid #FF8181;align-items:center}.nav *{color:#7B7B7B;font-family:'Nanum Gothic', sans-serif;font-weight:700}.nav a{text-decoration:none;color:#7B7B7B}.nav .button-nofill{padding:10px;border:2px solid #FF8181;background-color:white;color:#FF8181;-webkit-transition:all 0.5s;transition:all 0.5s}.nav .button-nofill:hover{background-color:#FF8181;color:white}.nav .button-fill{padding:10px;border:2px solid #FF8181;background-color:#FF8181;color:white;-webkit-transition:all 0.5s;transition:all 0.5s}.nav .button-fill:hover{background-color:white;color:#FF8181}.nav .highlight{color:#FF8181}.nav .inline{display:inline}.nav .gnb,.nav .logo,.nav .search{flex:none}.nav .logo{padding:10px;margin:0}.nav .logo img{display:block;height:50px}.nav .search{display:flex;align-items:center;margin-top:5px;margin-left:10px;height:40px;border:2px solid #FF8181;background-color:#FFEBEB}.nav .search .category{height:40px;width:80px;border:0;background-color:#FFCCCC;text-align-last:center}.nav .search .category:focus{outline:none}.nav .search .category option{text-shadow:0 1px 0 rgba(0,0,0,0.4)}.nav .search .input{flex:1 1;width:30vw;height:40px;margin:0 10px;padding:0;background-color:#FFEBEB;border-width:0}.nav .search .input:focus{outline:none}.nav .search .button{display:flex;align-items:center;justify-content:center;width:40px;height:40px;background-color:#FF8181;border:0;-webkit-transition:all 0.5s;transition:all 0.5s}.nav .search .button:focus{outline:none}.nav .search .button:hover{cursor:pointer}.nav .search .button img{width:30px;height:30px}.nav .menu{display:flex;margin-left:auto;height:70px}.nav .menu .signup{color:#FF8181}.nav .menu .signin{background-color:#FF8181;color:white}.nav .menu .common-align{display:flex;align-items:center;height:100%;padding:0 40px}

#Landing{height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center}#Landing *{color:#7B7B7B;font-family:'Nanum Gothic', sans-serif;font-weight:700}#Landing a{text-decoration:none;color:#7B7B7B}#Landing .button-nofill{padding:10px;border:2px solid #FF8181;background-color:white;color:#FF8181;-webkit-transition:all 0.5s;transition:all 0.5s}#Landing .button-nofill:hover{background-color:#FF8181;color:white}#Landing .button-fill{padding:10px;border:2px solid #FF8181;background-color:#FF8181;color:white;-webkit-transition:all 0.5s;transition:all 0.5s}#Landing .button-fill:hover{background-color:white;color:#FF8181}#Landing .highlight{color:#FF8181}#Landing .inline{display:inline}#Landing #btn{display:flex}#Landing .btn-landing{display:block;min-width:200px;margin:10px}@media (max-width: 768px){#Landing #btn{flex-direction:column}#Landing .btn-landing{width:95%}}


